function NumberFormatCustom3(props) {
    const { inputRef, onChange, data, ...other} = props;
    console.log("hola", props)
    const MAX_VAL = 2147483648;
    const MIN_VAL = -2147483648;
    const withValueLimit = ({ value }) => (value >= MIN_VAL && value <= MAX_VAL || value === "" || value=== "-");
    var noPrefix;
    var  noSuffix;

    return (
      <NumberFormat
        {...other}
        onValueChange={({ value: v }) => onChange({ target: {value: v } })}
        allowNegative
        isNumericString
        thousandsGroupStyle={data?.style && (data?.style["MaskingValue"]==="currency_rupees"
            || data?.style["MaskingValue"]==="dgroup2")
            ? 'lakh' : 'thousand'}
        prefix={noPrefix === true ? null : data.style && data.style["MaskingValue"]==="currency_dollar" ?  "$ "
        : data?.style && data?.style["MaskingValue"]==="currency_euro" ?  "€ " :
        data?.style && data?.style["MaskingValue"]==="currency_yen" ?  "¥ " :
        data?.style && data?.style["MaskingValue"]==="currency_naira" ?  "₦ " :
        data?.style && data?.style["MaskingValue"]==="dgroup3" ?  "" :
        data?.style && data?.style["MaskingValue"]==="currency_rupees" ?  "₹ " :
        data?.style && data?.style["MaskingValue"]==="currency_bahamas" ?  "B$ " :
        ""}

        suffix={noSuffix === true ? null : data?.style && data?.style["MaskingValue"]==="percentage" ?  " %" :
        data?.style && data?.style["MaskingValue"]==="currency_french" ?  " CHF" :
        ""}
        isAllowed={withValueLimit}
          decimalScale={0}

      />
    );
  };

